.intro {
   display: flex;
   justify-content: center;
   flex-direction: column;

   height: 100vh;
}

.intro-title {
   font-size: 32px;
}
.intro-title p:nth-child(2) {
   font-weight: bold;
   color: var(--color-1);
}

.intro-links {
   object-fit: cover;
   margin-top: 10px;
}

.intro-link-label {
   height: 15px;
}

.intro-link {
   width: 32px;
   filter: invert(var(--invert));
   padding: 3px;

   transition: opacity 0.1s ease-out;
}

.intro-link:hover {
   opacity: 0.8;
   cursor: pointer;
}