@media (max-width: 750px) {
   .showcase-c-item.selected {
      /*
      width: 50vw !important;
      height: 50vw !important;
      background-size: 90vw !important;
      */
      width: 350px !important;
      height: 350px !important;
      background-size: 400px;
   }

   .showcase-carousel-container {
      height: 200px !important;
   }
}

.showcase {
   padding-top: 30px;
   height: 1000px;
   width: 100vw !important;
   max-width: 100% !important;
   
}

.showcase-title {
   text-align: center;
   padding: 20px;
}

.showcase-links {

}

.showcase-link {
   width: 27px;
   padding-left: 6px;

   filter: invert(var(--invert));
}

.showcase-link:hover {
   opacity: 0.8;
}

.showcase-link-text {
   font-size: 14px;
}

.showcase-awards {
   display: flex;
   flex-wrap: wrap;
   flex-direction: row;
   gap: 6px;
   padding-top: 10px;
}

.showcase-award {
   background-color: var(--background-secondary);
   padding: 4px 6px 4px 6px;
   color: var(--foreground-primary);
   border-radius: 100px;
   font-size: 14px;

   display: flex;
   justify-content: center;
   align-items: center;
   gap: 3px;
}

.showcase-carousel-container {
   height: 250px;
   display: flex;

   overflow-x: auto;
   overflow-y: hidden;
   
}

.showcase-carousel {
   display: flex;
   flex-direction: row;
   align-items: center;
   position: relative;
}

.showcase-info-container {
   
}

.showcase-info {
   padding: 30px;
   position: relative;
}

.showcase-info h2 {
   font-size: 27px;
}

.showcase-info > pre {
   white-space: pre-line;
   font-size: 16px;
   padding-top: 10px;
   padding-bottom: 10px;
}

.showcase-info p {
   width: auto;
   font-style: italic;
}

.showcase-info-header {
   display: flex;
   flex-direction: row;
}

.showcase-info-header-left {
   flex: 2;
}

.showcase-info-header-right {
   flex: 1;
   text-align: right;
}

.showcase-c-item {
   border-radius: 1000px;
   
   background-size: 475px;
   background-position: center;
   background-repeat: no-repeat;

   min-width: 180px;
   min-height: 180px;
   width: 180px;
   height: 180px;
   margin: 10px;

   display: flex;
   justify-content: center;
   align-items: center;

   filter: saturate(0.5);

   transition: 0.25s;
}

.showcase-c-item:hover {
   filter: saturate(1);
   cursor: pointer;
}

.showcase-c-item > h3 {
   color: var(--foreground-primary);
   text-align: center;
   padding: 5px 10px 5px 10px;
   margin: 15px;
   background-color: var(--background-secondary-alpha);
   border-radius: 100px;

   transition: opacity 0.25s;
}

.showcase-c-item.selected {
   width: 450px;
   height: 450px;

   filter: saturate(1);
}

.showcase-c-item.selected h3 {
   opacity: 0;
}