.navbar {
   position: fixed;
   width: 100vw;
   height: 30px;
   top: 0%;

   display: flex;
   flex-direction: row;
   justify-content: center;
   align-items: center;
   gap: 10px;

   background-color: var(--background-primary);
}

.navbar-element.selected {
   text-decoration: underline;
}

.navbar-element:hover {
   text-decoration: underline;
   cursor: pointer;
}