.about {
   height: auto;
}

.about-top {
   flex: 1;

   display: flex;
   justify-content: flex-end;
   align-items: center;
   flex-direction: column;
}

.about-top > p {
   flex: 3;
   font-size: 16px;
   padding-top: 5px;
   padding-bottom: 15px;

   text-align: left;
}

.about-top > h2 {
   flex: 1;
}

.about-bottom {
   flex: 1;

   display: flex;
   justify-content: center;
   align-items: center;
   flex-wrap: wrap;
}

.about-bottom div {
   flex: 1;
}