.exp-group {
   height: 100%;
   padding-top: 10px;
}

.exp-group-title {
   text-align: center;
   height: auto;
   padding-bottom: 6px;
}

.exp-group-collection {
   border-radius: 100px;

   width: fit-content;
   margin: auto;
   
   background-color: var(--background-secondary);

   display: flex;
   justify-content: center;
   align-items: center;
}

.exp-group-label {
   text-align: center;
   height: 35px;
}

.exp-group-label > h3 {
   font-size: 14px;
   padding-top: 10px;
   font-weight: normal;
}

.exp-group-collection-container {
   flex: 2;
   padding: 0 15px 0 15px;
}

.exp-group-collection-container img {
   width: 36px;
   margin: 7px;

   border-radius: 10px;

   filter: invert(var(--invert));

   transition: 0.1s;
   outline: 0px solid gray;
}

.exp-group-collection-container img:hover {
   outline: 5px solid gray;
   cursor: pointer;
   border-radius: 100px;
}