/* main colors */
:root {
   --background-primary: #ffffff;
   --background-secondary: #cfcfcf;
   --background-secondary-alpha: #cfcfcf93;
   --foreground-primary: #111111;
   --foreground-secondary: #414141;
   --invert: 0.6;

   --color-1: #9d7fa5;
}
:root.darkmode {
   --background-primary: #303030;
   --background-secondary: #4b4b4b;
   --background-secondary-alpha: #4b4b4b93;
   --foreground-primary: #c9c9c9;
   --foreground-secondary: #b4b4b4;
   --invert: 0.3;
}

* {
   margin: 0;
   font-family: 'Inter', sans-serif;

   transition: background-color 0.3s, color 0.3s;
}

/* makes content scroll through the app div not the body */
.app {
   overflow-y: auto;
   overflow-x: hidden;
   height: 100vh;
   background-color: var(--background-primary);
   color: var(--foreground-primary);
}

body {
   overflow: hidden;
}

.page {
   width: 60vw;
   max-width: 700px;
   min-width: 300px;
   margin: auto;
}
